import { Component, OnInit, ViewChild } from '@angular/core';
import { FieldsService } from '../../shared/services/fields.service';
import { Observable, map } from 'rxjs';
import { CommonModule } from '@angular/common';
import { FormControl, FormGroup, ReactiveFormsModule } from '@angular/forms';
import {
  Branches,
  WebStorageService,
} from '../../shared/services/webStorage.service';

import { NzFlexModule } from 'ng-zorro-antd/flex';
import { BranchItemComponent } from '../branch-item/branch-item.component';
import { NzButtonModule } from 'ng-zorro-antd/button';
import { NzGridModule } from 'ng-zorro-antd/grid';
import { SvgQuoteComponent } from '../../shared/component/svg-quote/svg-quote.component';
import { SuccessComponent } from '../../shared/component/success/success.component';
import { RedirectService } from '../../shared/services/redirect.service';
import { ErrorViewComponent } from '../../shared/components/error-view/error-view.component';

type fields = {
  key: string;
  label: string;
  branches: { [key: string]: string };
}[];

type branchesForm = { [key: string]: FormControl<boolean | ''> };

@Component({
  selector: 'app-fields-list',
  standalone: true,
  imports: [
    CommonModule,
    ReactiveFormsModule,
    BranchItemComponent,
    NzFlexModule,
    NzButtonModule,
    NzGridModule,
    SvgQuoteComponent,
    SuccessComponent,
    ErrorViewComponent,
  ],
  providers: [FieldsService, WebStorageService],
  templateUrl: './fields-list.component.html',
  styleUrl: './fields-list.component.css',
})
export class FieldsListComponent implements OnInit {
  fieldsForm!: FormGroup;
  fieldsArray: any = [];
  branchesCached: Branches = {};
  countCheckedNum: number = 0;
  canSubmit: boolean = false;
  description: string =
    'Select at least one element from which to receive random quotes.';

  //to-do gestire gli errori component con async
  getFieldError: any = null;

  constructor(
    private fieldsService: FieldsService,
    private webStorageService: WebStorageService,
    private redirectService: RedirectService
  ) {}
  ngOnInit(): void {
    this.branchesCached = this.webStorageService.getBranches();
    this.buildForm();
  }

  buildForm() {
    const isBranchesCached = !!this.branchesCached;
    //to-do cache
    this.fieldsService.getFields().subscribe((fields: fields) => {
      const fieldsMap: { [key: string]: FormGroup } = {};

      for (const [fieldKey, fieldValue] of Object.entries(fields)) {
        let branches: branchesForm = {};
        let branchesArray: any[] = [];
        for (const [branchKey, branchValue] of Object.entries(
          fieldValue.branches
        )) {
          branches[branchKey] = new FormControl<boolean | ''>(
            isBranchesCached
              ? this.initCountChecked(fieldKey, branchKey)
              : false,
            {
              nonNullable: true,
            }
          );
          branchesArray.push([branchKey, branchValue]);
        }
        fieldsMap[fieldKey] = new FormGroup({ ...branches });
        this.fieldsArray.push({
          key: fieldKey,
          label: fieldValue.label,
          value: branchesArray,
        });
      }
      this.fieldsForm = new FormGroup({ ...fieldsMap });
      this.canSubmit = this.countCheckedNum > 0;
    });
  }

  initCountChecked(fieldKey: string, branchKey: string): boolean {
    let isChecked = this.branchesCached[fieldKey]?.indexOf(branchKey) >= 0;

    if (isChecked) {
      this.countCheckedNum += 1;
    }

    return isChecked;
  }

  countChecked(event: any) {
    if (event) {
      this.countCheckedNum += 1;
    } else {
      this.countCheckedNum -= 1;
    }
    this.canSubmit = this.countCheckedNum > 0;
  }

  onSubmit() {
    //console.log(this.fieldsForm.value);
    let branchesSelectedObj: Branches = {};
    for (const [fieldKey, branchesChecked] of Object.entries(
      this.fieldsForm.value
    )) {
      let branchesSelected: any[] = [];
      for (const [branchKey, isChecked] of Object.entries(
        branchesChecked as { [key: string]: boolean }
      )) {
        if (isChecked) {
          branchesSelected.push(branchKey);
        }
      }
      if (branchesSelected.length > 0) {
        branchesSelectedObj[fieldKey] = branchesSelected;
      }
    }
    //console.log(Object.keys(branchesSelectedObj).length);
    this.webStorageService.saveBranches(branchesSelectedObj);
    this.redirectService.redirectToQuote();
    // // console.log(send);
  }

  checkForm() {}
}
