import {
  Component,
  EventEmitter,
  Input,
  Output,
  forwardRef,
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { NzTagModule } from 'ng-zorro-antd/tag';
import { NzIconModule } from 'ng-zorro-antd/icon';
import { NzFlexModule } from 'ng-zorro-antd/flex';
import { CommonModule } from '@angular/common';

@Component({
  selector: 'app-branch-item',
  standalone: true,
  imports: [NzTagModule, NzIconModule, NzFlexModule, CommonModule],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => BranchItemComponent),
      multi: true,
    },
  ],
  templateUrl: './branch-item.component.html',
  styleUrl: './branch-item.component.less',
})
export class BranchItemComponent implements ControlValueAccessor {
  @Input()
  label: string = '';

  @Output()
  countChecked: EventEmitter<boolean> = new EventEmitter<boolean>();

  isSelected!: boolean;
  disabled = false;
  onChange: any = () => {};
  onTouched: any = () => {};

  writeValue(obj: any): void {
    this.isSelected = obj;
  }

  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }
  changeCustom() {
    this.isSelected = !this.isSelected;
    this.countChecked.emit(this.isSelected);
    this.onChange(this.isSelected);
  }

}
